@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

.read-more {
  display: flex;
  align-items: center;
  margin-left: 16px;
  margin-top: 10px;
  font-family: "TTLakes-Medium", serif;
  font-weight: 700;
  cursor: pointer; }

.read-more__expand {
  margin: 0 8px;
  cursor: pointer; }

.picture {
  width: 100%;
  padding-top: 20px; }

@media (min-width: 320px) {
  .content {
    padding-left: 16px; } }

@media (min-width: 768px) {
  .content {
    margin-top: 32px;
    margin-left: 12px;
    display: flex;
    min-width: 320px;
    width: 100%; }
  .content__text {
    min-width: 30%;
    line-height: 20px;
    font-family: "TTLakes-Regular", serif; }
  .content__text-2 {
    font-family: "TTLakes-Regular", serif;
    display: block;
    width: 100%; }
  .content__text-1 {
    width: 100%; }
  .picture {
    min-width: 40%;
    border-radius: 2%;
    margin-left: 48px;
    border-radius: 10px;
    padding: 0; }
  .header__inn {
    width: 100%; } }

@media (min-width: 1120px) {
  .container {
    position: relative;
    max-width: 1120px;
    margin: 20px auto 0 auto;
    padding-top: 32px; }
  .picture {
    max-width: 40%;
    left: 50%; }
  .content__text {
    max-width: 50%;
    margin-left: 32px;
    line-height: 20px;
    height: 300px;
    overflow: hidden; }
  .content__text-1 {
    min-width: 100%;
    padding-right: 36px; }
  .content__text-2 {
    min-width: 100%;
    padding-right: 36px; }
  .picture {
    padding-right: 29px;
    max-width: 530px; } }

.content__text-3 {
  height: 0;
  overflow: hidden; }

.--active {
  height: auto;
  padding-right: 36px; }

.content__readmore-btn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative; }

.content__readmore-btn::before {
  content: url(./static/readmore.svg);
  width: 20px;
  height: 20px;
  left: -20px;
  position: absolute; }
