@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

.content-p {
  text-align: left;
  font-size: 14px;
  border-bottom: 1px solid #d9fff5;
  max-width: 95%;
  margin-left: 16px;
  font-family: "TTLakes-Regular", serif;
  color: #1b1c21; }

.footer1 {
  color: #7e7e82;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0px;
  text-align: center;
  margin-top: 20px;
  font-family: "TTLakes-Regular", serif; }

@media (min-width: 620px) {
  .footer1 {
    display: flex;
    justify-content: space-around; } }

.footer.footer__text-1 {
  margin-top: 50px;
  font-family: "TTLakes-Regular", serif; }

.content__link {
  font-family: "TTLakes-Bold";
  font-weight: bold;
  position: relative; }

.content__link::after {
  content: ">";
  color: #ff3e79;
  position: absolute;
  top: 0;
  left: 270px; }
