@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

.slider-container3 {
  overflow: hidden;
  width: 75%;
  margin-top: 16px;
  margin-left: 40px;
  min-width: 240px; }

.swiper-wrapper3 {
  display: flex;
  width: 80%;
  height: 250px; }

@media (min-width: 600px) {
  .swiper-wrapper3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    width: 100%; }
  .slider-container3 {
    width: 100%;
    margin-left: 10px; } }

.swiper-wrapper__card3 {
  padding: 20px;
  border: 1px solid #ecebeb;
  border-radius: 5px;
  height: 200px;
  width: 240px;
  color: #1b1c21;
  font-size: 16px;
  flex-shrink: 0;
  margin-right: 24px;
  background-color: #ffffff;
  font-family: "TTLakes-Regular", sans-serif;
  position: relative;
  min-width: 240px; }

@media (min-width: 600px) {
  .swiper-wrapper__card3 {
    max-height: 65px;
    width: 95%;
    border: none;
    border-bottom: 1px solid #ecebeb;
    display: flex;
    justify-content: space-between; }
  .title-hidden {
    display: none; } }

.swiper-wrapper-main__btn3 {
  width: 119px;
  height: 32px;
  background-color: #ff3e79;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #ffffff;
  position: absolute;
  bottom: 16px;
  left: 110px;
  cursor: pointer; }

@media (min-width: 600px) {
  .swiper-wrapper-main__btn3 {
    position: sticky; } }

.swiper-wrapper__link-btn3 {
  text-decoration: none;
  background-image: url(./static/dropdownWhite.svg);
  width: 7px;
  height: 10px;
  color: #000;
  cursor: pointer; }

.swiper-wrapper-main__btn3.hidd {
  opacity: 0; }

.header-title3 {
  margin-top: 16px;
  margin-left: 20px;
  display: none;
  color: #7e7e82; }

@media (min-width: 600px) {
  .header-title3 {
    display: block; } }

.header-title3__cont {
  display: flex;
  background-color: transparent;
  height: 65px;
  width: 90%;
  justify-content: space-between;
  font-family: "TTLakes-Regular", serif; }

.swiper-wrapper__card3.hidden {
  display: none; }

.title-hidden {
  color: #7e7e82;
  font-family: "TTLakes-Regular", serif; }
