@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

a {
  text-decoration: none;
  color: #7e7e82;
  font-family: "TTLakes-Regular", serif; }

.list {
  display: flex;
  overflow: scroll; }

@media (min-width: 1120px) {
  .list {
    justify-content: flex-start; } }

.list__btn {
  width: 246px;
  height: 40px;
  line-height: 36px;
  text-align: center;
  color: transparent;
  white-space: nowrap;
  margin-left: 32px;
  padding-left: 15px;
  padding-right: 15px;
  border: 2px solid #f8f8f8; }

.list__btn:hover {
  width: 246px;
  height: 40px;
  color: #7e7e82;
  border: 2px solid #d9fff5;
  background-color: #ffffff;
  border-radius: 6px; }

::-webkit-scrollbar {
  width: 0; }
