@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding-right: 25px; }

.title__main {
  position: relative; }

.title__h1 {
  margin-left: 12px;
  font-family: "TTLakes-Bold", sans-serif;
  font-size: 28px;
  line-height: 40px;
  color: #1b1c21; }

@media (min-width: 320px) {
  .title__h1::before {
    content: url(./static/highlight.svg);
    position: relative;
    top: 10px;
    left: -11px; } }

@media (min-width: 1120px) {
  .title__h1 {
    margin-left: 30px; }
  .title__h1::before {
    content: url(./static/highlight.svg);
    position: relative;
    top: 10px;
    left: -20px; } }

@media (min-width: 768px) {
  .title__h1 {
    margin-left: 20px; }
  .title__h1::before {
    content: url(./static/highlight.svg);
    position: relative;
    top: 10px;
    left: -20px; } }

.title__main {
  display: flex;
  align-items: center; }

.main__button--hidden {
  display: none; }

@media (min-width: 1120px) {
  .main__button--hidden {
    display: flex;
    align-items: center; }
  .header__right-menu--repair {
    margin-right: 5px; }
  .header__right-menu--repair {
    margin-right: 2em; }
  label {
    margin-right: 10px; }
  .title__main,
  .main__button--hidden {
    margin-top: 20px; } }
