@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

.popup {
  background-color: #000;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 2;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.808);
  display: none; }

.popup__content {
  width: 320px;
  background-color: #ffffff;
  height: 100%;
  font-family: "TTLakes-Regular", serif; }

a {
  text-decoration: none;
  color: #1b1c21;
  margin-left: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: auto; }

.header-menu__inn {
  display: flex;
  flex-wrap: nowrap;
  margin: 18px 24px 0px 24px;
  align-items: center; }

.header-menu__btn-menu {
  display: flex;
  /* margin-right: 16px; */
  justify-content: flex-end; }

.menu-button {
  border: none;
  display: flex;
  background-color: transparent;
  align-items: center; }

.menu-button__back {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background: #ff3e79;
  border-radius: 50%;
  cursor: pointer; }

.header-menu__menu-logo {
  display: flex;
  width: auto; }

.header-menu__logo {
  margin-left: 10px; }

.header-menu__btn-menu {
  width: auto;
  margin-left: 55px; }

.main-menu__link {
  list-style: none;
  margin-bottom: 32px;
  display: flex;
  justify-content: flex-start;
  align-items: center; }

ul {
  padding-left: 0; }

.main-menu__list {
  margin: 50px 0px; }

.main-menu__link :hover {
  opacity: 0.5; }

.main-menu__highlight {
  background-image: url(./static/highlight.svg);
  width: 4px;
  height: 32px;
  opacity: 0;
  transition: 300ms;
  margin-right: 22px; }

a:hover .main-menu__highlight {
  opacity: 1;
  transition: 500ms; }

.footer-menu__cont {
  margin: 10px 0px 32px 24px; }

.footer-menu__menu {
  display: flex; }

.footer-menu__mail {
  font-size: 16px;
  margin-top: 16px;
  color: #1b1c21; }

.footer-menu__tel {
  font-style: 24px;
  margin-top: 12px;
  line-height: 32px;
  font-family: "TTLakes-Regular", sans-serif; }

.footer-menu__lang {
  display: flex; }

.footer-menu__btn {
  border: none;
  display: flex;
  background-color: transparent;
  align-items: center;
  margin-top: 40px;
  cursor: pointer; }

.footer-menu__btn:hover {
  opacity: 0.5; }
