@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

#popup2 {
  border: 2px solid green;
  width: 100%;
  height: 100%; }

.popup2__content {
  background-color: #ffffff;
  width: 440px;
  min-height: 1200px;
  /* border: 2px solid seagreen; */
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 32px; }

input {
  border-radius: 8px;
  height: 48px;
  width: 376px;
  border: 1px solid #eaeaea;
  margin-bottom: 16px;
  font-family: "TTLakes-Regular", sans-serif; }

#text {
  border-radius: 8px;
  border: 1px solid #eaeaea;
  width: 376px;
  font-family: "TTLakes-Regular", sans-serif;
  color: #a09f9f; }

.popup2__title {
  margin-bottom: 16px;
  margin-top: 40px; }

.popup2__close {
  position: absolute;
  display: block;
  height: 40px;
  width: 40px;
  background-color: #ff3e79;
  border-radius: 50%;
  text-decoration: none;
  color: #ffffff;
  text-align: center;
  top: 35px;
  right: 450px;
  /* padding-top: 10px; */
  cursor: pointer; }

#popup2__btn {
  margin-top: 10px; }

.popup2__title {
  font-family: "TTLakes-Regular", sans-serif;
  font-size: 24px; }

.content {
  font-family: "TTLakes-Regular", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0px;
  text-align: left;
  color: #7e7e82; }

.aferta {
  text-decoration: none;
  color: #ff3e79; }

.popup1__btn-send {
  width: 136px;
  height: 32px;
  background-color: #ff3e79;
  border: none;
  border-radius: 16px;
  margin-top: 16px;
  color: #ffffff;
  cursor: pointer; }

.dropdown {
  margin-left: 10px; }
