@charset "UTF-8";
@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0;
  font-family: "TTLakes-Medium"; }

@media (min-width: 1400px) {
  body {
    display: flex;
    flex-wrap: nowrap;
    width: 100%; }
  .menu-button.close {
    display: none; }
  .menu-button.hid {
    display: none; }
  .header-menu__logo {
    cursor: pointer; } }

@media (min-width: 1400px) and (min-width: 1450px) {
  body {
    max-width: 1440px;
    margin: 0 auto; } }

@media (min-width: 1400px) {
  .container-1 {
    display: block; }
  .container-2 {
    width: 80%; } }

.container-1 {
  min-width: 320px;
  height: 1800px;
  background-color: #ffffff; }

@media (max-width: 1400px) {
  .container-1 {
    display: none; } }

/* ---------------Header------------------- */
.slider-brand {
  margin-top: 24px;
  height: auto; }

@media (max-width: 600) {
  .slider-brand {
    margin-top: 2em;
    height: auto; } }

.header-slider {
  width: 100%;
  min-width: 320px;
  height: 56px;
  background: linear-gradient(90deg, #f2f2f2 0%, #ffffff 98.61%);
  border-top: 1px solid #d9fff5; }

@media (max-width: 600px) {
  .header-slider {
    height: 80px; } }

.header-slider__title {
  padding: 15px 0px 0px 32px;
  font-size: 16px;
  text-transform: uppercase;
  font-family: "TTLakes-Regular", sans-serif;
  color: #7e7e82; }

/* --------------Slider------------------ */
.swiper-wrapper__card {
  padding: 5px;
  /* border: 1px solid black; */
  min-height: 100px;
  min-width: 25%;
  color: #000;
  font-size: 16px;
  flex-shrink: 0;
  margin-right: 0;
  margin-bottom: 0; }

.slider-container {
  width: 100%;
  overflow: hidden;
  height: 200px; }

/* -------.slider-container(active)---------*/
.active {
  height: auto; }

.swiper-wrapper {
  padding: 0;
  flex-wrap: wrap; }

/* -------КнопкиСсылки------ */
.swiper-wrapper__link-company {
  height: 72px;
  min-width: 224px;
  max-width: 240px;
  border-radius: 6px;
  border: 1px solid #eaeaea;
  background-color: #ffffff;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 10px 0px 0px 15px; }

@media (max-width: 600px) {
  .swiper-wrapper__link-company {
    margin-left: 10%;
    min-width: 240px; } }

/* --------СсылкиЛого-------- */
.swiper-wrapper-main__logo {
  width: auto;
  max-width: 128px;
  height: auto; }

.swiper-wrapper-main__btn {
  width: 40px;
  height: 40px;
  border: 2px solid #ff3e79;
  border-radius: 20px;
  display: flex;
  align-items: center;
  cursor: pointer; }

.swiper-wrapper__link-btn {
  background-image: url(./static/dropdown.svg);
  width: 7.5px;
  height: 12px;
  margin-left: 15px;
  background-repeat: no-repeat;
  cursor: pointer; }

/* ---------Читать далее------------*/
.read-more__btn {
  margin: 0px 0px 0px 5px;
  border: none;
  background-color: transparent;
  font-family: "TTLakes-Regular", Arial, sans-serif;
  cursor: pointer; }

.read-more__img {
  margin-left: 32px; }

.swiper-pagination {
  display: none; }

@media (max-width: 600px) {
  .read-more__btn,
  .read-more__img {
    display: none; } }

@media (max-width: 600px) {
  .swiper-wrapper__card {
    width: 100%;
    max-height: 100px; }
  .swiper-wrapper {
    flex-wrap: nowrap; }
  .swiper-pagination {
    display: block; } }
